import React from 'react';

function Card({ name, email, id }) {
    return(
        <div className='tc bg-light-green dib br3 pa3 ma2 grow bw2 shadow-5'>
            <img alt='robots' src={`https://robohash.org/${id}test?200x200`} />
            <header>
                <h2>{name}</h2>
                <p>{email}</p>
            </header>
        </div>
    );
}

export default Card;